                    <button @click="toggleMenu" type="button">
                        <svg class="w-5 h-5" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
                            <path d="M6 10a2 2 0 114 0 2 2 0 11-4 0zM10 2a2 2 0 110 4 2 2 0 010-4zM10 14a2 2 0 110 4 2 2 0 010-4z" />
                        </svg>
                    </button>

                    <div
                        v-if="menuOpen"
                        :class="['absolute', 'top-16', 'right-10', ' bg-white', 'shadow-md', 'md:block', 'border']"
