                        </div>
                    </div>
                </div>
                <div class="relative mb-4">
                    <input v-model="searchTerm" type="text" class="w-full p-2 pl-10 border rounded-lg" placeholder="Busqueda" />
                </div>
                <div class="p-4">
                    <div class="space-y-2">
                        <template v-for="(listapacientes, letra) in filteredPacientesPorLetra" :key="letra">
                            <div class="flex items-center justify-between">
                                <h2 class="text-lg font-semibold text-[#0077B6]">{{ letra }}</h2>
                                <svg
                                    xmlns="http://www.w3.org/2000/svg"
                                    width="24"
                                    height="24"
                                    viewBox="0 0 24 24"
                                    fill="none"
                                    stroke="currentColor"
                                    stroke-width="2"
                                    stroke-linecap="round"
                                    stroke-linejoin="round"
                                    class="h-4 w-4 text-[#0077B6]"
                                >
                                    <line x1="10" x2="21" y1="6" y2="6"></line>
                                    <line x1="10" x2="21" y1="12" y2="12"></line>
                                    <line x1="10" x2="21" y1="18" y2="18"></line>
                                    <path d="M4 6h1v4"></path>
                                    <path d="M4 10h2"></path>
                                    <path d="M6 18H4c0-1 2-2 2-3s-1-1.5-2-1"></path>
