                            </div>
                            <details class="group" v-for="paciente in listapacientes" :key="paciente.id">
                                <summary class="flex items-center justify-between cursor-pointer bg-gray-100 p-2 rounded">
                                    <span class="text-sm text-gray-900">{{ paciente.nombre }}</span>
                                    <div class="flex items-center gap-2">
                                        <svg
                                            xmlns="http://www.w3.org/2000/svg"
                                            width="24"
                                            height="24"
                                            viewBox="0 0 24 24"
                                            fill="none"
                                            stroke="currentColor"
                                            stroke-width="2"
                                            stroke-linecap="round"
