                                            <path d="M19 6v14c0 1-1 2-2 2H7c-1 0-2-1-2-2V6"></path>
                                            <path d="M8 6V4c0-1 1-2 2-2h4c1 0 2 1 2 2v2"></path>
                                        </svg>
                                        Desactivar
                                    </button> -->
                                </div>
                            </details>
                            <hr />
                        </template>
                    </div>
                </div>
                <div class="fixed bottom-0 left-0 right-0 p-4 bg-white shadow"></div>
            </div>
        </div>
    </div>
    <div class="bottom-0 w-full bg-white p-4 sticky">
        <router-link
            to="/register-paciente-view"
            @medicNew="onPacienteCreated"
            class="block w-full border-2 bg-[#072b52] hover:bg-[#173657] text-white p-3 rounded-lg active:text-[#173657] active:border-[#173657] active:bg-white active:border-2"
            >Registrar Paciente</router-link
        >
    </div>
    <ModalComponent v-if="modalValue" @close="hideModal">
        <EditPacienteModal v-if="selectedPaciente" :paciente="selectedPaciente" @close="hideModal" @medicEdited="onPacienteEdited" />
    </ModalComponent>
    <!-- <ModalComponent2 v-if="isModalOpen" @close="closeModal" /> -->
</template>

<script lang="ts" setup>
import router from '@/router';
import store from '@/store';
import ModalComponent from '@/components/page/login/components/ModalComponent.vue';
import EditPacienteModal from '../components/page/home/pacientes/EditPacienteModal.vue';
import { computed, nextTick, onMounted, reactive, ref } from 'vue';
import ModalComponent2 from '@/components/page/login/components/ModalComponent2.vue';
import homeService from '@/components/page/login/helper/homeService';
import { Paciente } from '@/components/page/login/interfaces/Paciente';

const pacientes = reactive<Paciente[]>([]);

const menuOpen = ref(false);

const searchTerm = ref('');

const toggleMenu = () => {
    menuOpen.value = !menuOpen.value;
    console.log('stateAuth', store.state.authentication);
};
function regresar() {
    router.push({ name: 'home' });
}
const modalValue = ref(false);
function hideModal() {
    modalValue.value = false;
}
function onPacienteEdited(newInfo: Paciente) {
    console.log('Nuevo Info:', newInfo);
    const index = pacientes.findIndex((m) => m.id === newInfo.id);
    if (index !== -1) {
        pacientes[index] = newInfo;
    }
}

const selectedPaciente = ref<Paciente | null>(null);

function showModal(paciente: Paciente) {
    selectedPaciente.value = { ...paciente };
    console.log('abrir:', modalValue.value);
    modalValue.value = true;
}
const isModalOpen = ref(false);

const closeModal = () => {
    isModalOpen.value = false;
};

const filteredPacientesPorLetra = computed(() => {
    const filteredPacientes = pacientes.filter((paciente) => paciente.nombre.toLowerCase().includes(searchTerm.value.toLowerCase()));

    const pacientesOrdenados = [...filteredPacientes].sort((a, b) => a.nombre.localeCompare(b.nombre));
    const grupos: { [key: string]: Paciente[] } = {};

    pacientesOrdenados.forEach((paciente) => {
        const primeraLetra = paciente.nombre.charAt(0).toUpperCase();
        if (!grupos[primeraLetra]) {
            grupos[primeraLetra] = [];
        }
        grupos[primeraLetra].push(paciente);
    });

    return grupos;
});

const fetchPacientes = async () => {
    const response = await homeService.getPacientesBoard();
    pacientes.push(...response.data.content);
};

const onPacienteCreated = async (newPaciente: Paciente) => {
    console.log('Nuevo Paciente:', newPaciente);
    pacientes.push(newPaciente);
    await nextTick();
};
onMounted(fetchPacientes);
</script>
<style scope="">
.group-content {
    display: grid;
    justify-items: baseline;
}
</style>
