<template>
    <div class="bg-card text-card-foreground">
        <div class="flex items-center justify-center rounded p-4 bg-blue-900 text-white">
            <h1 class="text-lg font-semibold">Editar</h1>
        </div>
        <hr />
        <div class="space-y-6 md:space-y-0 md:grid md:grid-cols-2 md:gap-4 pt-2">
            <div class="border-b pb-4 col-span-2 md:col-span-1">
                <h2 class="text-xl font-semibold text-[#072b52]">Paciente</h2>
                <div class="mt-2 form-group">
                    <input
                        type="text"
                        class="w-full mt-1 p-2 border rounded form-input focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                        v-model="infoPaciente.nombre"
                        required
                        placeholder=""
                        :class="{ 'border-red-500': v$.infoPaciente.nombre.$error }"
                    />
                    <label class="block text-sm font-medium form-label">Nombre completo</label>
                    <span v-if="v$.infoPaciente.nombre.$error" class="flex font-sans text-sm text-red-600">Este campo es obligatorio</span>
                </div>
                <div class="mt-2 grid grid-cols-2 gap-4">
                    <div class="mt-5 form-group">
                        <input
                            type="text"
                            class="w-full mt-1 p-2 border rounded form-input focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                            v-model="infoPaciente.documento"
                            disabled
                            placeholder=""
                        />
                        <label class="block text-sm font-medium form-label">Documento</label>
                    </div>
                </div>
            </div>
            <div class="border-b pb-4 col-span-2 md:col-span-1">
                <h2 class="text-xl font-semibold text-[#072b52]">Contactos</h2>
                <div class="mt-2 form-group">
                    <input
                        type="email"
                        class="w-full mt-1 p-2 border rounded form-input focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                        v-model="infoPaciente.email"
                        disabled
                        placeholder=""
                    />
                    <label class="block text-sm font-medium form-label">E-mail</label>
                </div>
                <div class="mt-7 form-group">
                    <input
                        type="text"
                        class="w-full mt-1 p-2 border rounded form-input focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                        v-model="infoPaciente.telefono"
                        required
                        placeholder=""
                        :class="{ 'border-red-500': v$.infoPaciente.telefono.$error }"
                    />
                    <label class="block text-sm font-medium form-label">Teléfono o celular</label>
                    <span v-if="v$.infoPaciente.telefono.$error" class="flex font-sans text-sm text-red-600">Este campo es obligatorio</span>
                </div>
            </div>
            <div class="col-span-2">
                <h2 class="text-xl font-semibold text-[#072b52]">Dirección</h2>
                <div class="mt-2 grid grid-cols-1 gap-4 md:grid-cols-2">
                    <div class="mt-2 form-group">
                        <input
                            type="text"
                            class="w-full mt-1 p-2 border rounded form-input focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                            v-model="infoPaciente.direccion.calle"
                            required
                            placeholder=""
                            :class="{ 'border-red-500': v$.infoPaciente.direccion.calle.$error }"
                        />
                        <label class="block text-sm font-medium form-label">Calle</label>
                        <span v-if="v$.infoPaciente.direccion.calle.$error" class="flex font-sans text-sm text-red-600"
                            >Este campo es obligatorio</span
                        >
                    </div>
                    <div class="grid grid-cols-2 gap-4">
                        <div class="mt-2 form-group">
                            <input
                                type="text"
                                class="w-full mt-1 p-2 border rounded form-input focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                                v-model="infoPaciente.direccion.numero"
                                required
                                placeholder=""
                            />
                            <label class="block text-sm font-medium form-label">Número</label>
                        </div>
                        <div class="mt-2 form-group">
                            <input
                                type="text"
                                class="w-full mt-1 p-2 border rounded form-input focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                                v-model="infoPaciente.direccion.complemento"
                                required
                                placeholder=""
                            />
                            <label class="block text-sm font-medium form-label">Complemento</label>
                        </div>
                    </div>
                    <div class="mt-5 form-group">
                        <input
                            type="text"
                            class="w-full mt-1 p-2 border rounded form-input focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                            v-model="infoPaciente.direccion.ciudad"
                            required
                            placeholder=""
                            :class="{ 'border-red-500': v$.infoPaciente.direccion.ciudad.$error }"
                        />
                        <label class="block text-sm font-medium form-label">Ciudad</label>
                        <span v-if="v$.infoPaciente.direccion.ciudad.$error" class="flex font-sans text-sm text-red-600"
                            >Este campo es requerido</span
                        >
                    </div>
                    <div class="grid grid-cols-2 gap-4">
                        <div class="mt-5 form-group">
                            <select
                                v-model="infoPaciente.direccion.estado"
                                class="w-full mt-1 p-2 border rounded focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                                required
                            >
                                <option>{{ infoPaciente.direccion.estado }}</option>
                                <option>PR</option>
                                <option>PS</option>
                                <option>MG</option>
                            </select>
                            <label class="block text-sm font-medium form-label">Estado</label>
                        </div>
                        <div class="mt-5 form-group">
                            <input
                                type="text"
                                class="w-full mt-1 p-2 border rounded form-input focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                                v-model="infoPaciente.direccion.postal"
                                required
                                placeholder=""
                                :class="{ 'border-red-500': v$.infoPaciente.direccion.postal.$error }"
                            />
                            <label class="block text-sm font-medium form-label">Postal</label>
                            <span v-if="v$.infoPaciente.direccion.postal.$error" class="flex font-sans text-sm text-red-600"
                                >Este campo es obligatorio</span
                            >
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="mt-4 flex space-x-4">
            <button
                @click="handleSubmit()"
                class="p-2 rounded-lg font-medium flex-1 focus:outline-none focus:ring-2 border-2 bg-[#072b52] text-white hover:bg-blue-900 active:border-blue-900 active:text-blue-900 active:bg-white active:border-2"
            >
                Concluir edición
            </button>
            <button
                @click="$emit('close')"
                class="p-2 rounded-lg font-medium flex-1 focus:outline-none focus:ring-2 border border-[#003366] hover:bg-primary/10 text-[#003366] active:bg-[#003366] active:text-white"
            >
                Cancelar
            </button>
        </div>
    </div>
</template>

<script setup lang="ts">
import { reactive, defineProps, defineEmits } from 'vue';
import { Paciente } from '../../login/interfaces/Paciente';
import PacienteService from '../../login/helper/PacienteService';
import { required } from '@vuelidate/validators';
import useVuelidate from '@vuelidate/core';

const props = defineProps<{
    paciente: Paciente;
}>();
const infoPaciente = reactive<Paciente>({ ...props.paciente });
const rules = {
    infoPaciente: {
        nombre: { required },
        telefono: { required },
        direccion: {
            calle: { required },
            ciudad: { required },
            estado: { required },
            postal: { required },
        },
    },
};
const v$ = useVuelidate(rules, { infoPaciente });

const emit = defineEmits(['pacienteEdited', 'close']);
async function handleSubmit() {
    const isValid = await v$.value.$validate();
    if (!isValid) {
        // Formulario no válido, muestra errores
        return;
    }
    try {
        const response = await PacienteService.editPaciente(infoPaciente);
        const editPaciente: Paciente = response.data;
        emit('pacienteEdited', editPaciente);
        alert('Paciente Editado');
    } catch (error) {
        console.error('error', error);
    }
}
</script>

<style scoped>
.form-input:focus + .form-label,
.form-input:not(:placeholder-shown) + .form-label {
    transform: translateY(-12px) scale(0.7);
    transform-origin: left top;
    color: hsl(var(--muted-foreground));
}

.form-label {
    position: absolute;
    top: 15px;
    left: 5px;
    transition:
        transform 0.5s,
        color 0.3s;
}

.form-group {
    position: relative;
    display: grid;
}

.form-group select:focus-within + .form-label,
.form-group select:valid + .form-label {
    transform: translateY(-60%) scale(0.7);
    transform-origin: left top;
    color: hsl(var(--muted-foreground));
}
</style>
