import axios from 'axios';
import authHeader from './auth-header';
import type { Medico } from '../interfaces/Medico';

// const API_URL = 'http://localhost:8080/';
const API_URL = '/api/';
class HomeService {
    async getMedicosBoard() {
        return await axios.get(API_URL + 'medicos', { headers: authHeader() });
    }

    async getPacientesBoard() {
        return await axios.get(API_URL + 'pacientes', { headers: authHeader() });
    }

    async getConsultasBoard() {
        return await axios.get(API_URL + 'consultas', { headers: authHeader() });
    }
    // async createMedico(medico: Medico) {
    //     return await axios.post(API_URL + `medicos/registrar`, { medico }, { headers: authHeader() });
    // }
}

export default new HomeService();
